import React, { useState } from "react";
import {
  Box,
  Button,
  Typography,
  Grid,
  Dialog,
  DialogActions,
  DialogContent,
  DialogTitle,
  CircularProgress,
} from "@mui/material";
import PropTypes from "prop-types";
import axiosClient from "../axiosClient"; // Import axiosClient
import { toast } from 'react-toastify'; 
import 'react-toastify/dist/ReactToastify.css'; 

const TileView2 = ({ papersData, onTileClick }) => {
  // Ensure papersData is an array
  const dataArray = Array.isArray(papersData) ? papersData : [];

  // State for managing the dialog
  const [open, setOpen] = useState(false);
  const [selectedItem, setSelectedItem] = useState(null);
  // const [error, setError] = useState("");
  const userId = localStorage.getItem("USER_ID");
  const [loading, setLoading] = useState(false);

  const handleEnrollClick = (item, event) => {
    event.stopPropagation(); // Prevent the click event from triggering the tile click
    setSelectedItem(item);
    setOpen(true);
  };

  const handleViewClick = (item) => {
      onTileClick(item); // Pass the selectedItem to the onTileClick function
      handleClose();
  };

  const handleClose = () => {
    setOpen(false);
    setSelectedItem(null);
  };

  const handleAccept = async () => {
    if (selectedItem) {
      setLoading(true);
      // Handle the acceptance logic here
      try {
        const dataToSubmit = {
          paper_id: selectedItem._id,
          user_id: userId,
        };

        // Submit the data to your backend using Axios
        console.log(dataToSubmit)
        const response = await axiosClient.post("/paper-enroll/create",dataToSubmit); 
        if (response.data.status) {
          toast.success("Enroll to paper successfully!!!");
          onTileClick(selectedItem); // Pass the selectedItem to the onTileClick function
        } else if (!response.data.status) {
          toast.error(response.data.msg);
        }
      } catch (error) {
        console.error("enroll error" + error);
      } finally {
        setLoading(false);
      }
      handleClose();
    }
  };

  return (
    <Box sx={{ padding: 2 }}>
      <Grid container spacing={2}>
        {dataArray.map((file) => (
          <Grid item xs={12} sm={6} md={4} lg={3} key={file.id}>
            <Box
              sx={{
                width: "100%",
                height: 0,
                paddingTop: "100%", // 1:1 Aspect Ratio
                position: "relative",
                display: "flex",
                flexDirection: "column",
                justifyContent: "center",
                alignItems: "center",
                border: "1px solid #ccc",
                borderRadius: 1,
                boxShadow: 2,
                backgroundColor: "white",
                overflow: "hidden",
                cursor: "pointer", // Make the whole box look clickable
              }}
              // onClick={() => onTileClick(file)} // Handle tile click
            >
              <Box
                sx={{
                  position: "absolute",
                  top: 0,
                  bottom: 0,
                  left: 0,
                  right: 0,
                  display: "flex",
                  flexDirection: "column",
                  justifyContent: "center",
                  alignItems: "center",
                }}
              >
                <Typography variant="h6">{file.title}</Typography>
                <Typography variant="body2">
                  by {file.teacher.full_name}
                </Typography>
                <Typography variant="body2">price: {file.price}</Typography>
              </Box>
              <Box
                sx={{
                  position: "absolute",
                  bottom: 0,
                  left: 0,
                  right: 0,
                  display: "flex",
                  justifyContent: "center",
                  padding: 1,
                }}
              >
                {(!file.isPurchase && file.teacher_id !== userId) && (
                  <Button
                    variant="contained"
                    color="primary"
                    onClick={(event) => handleEnrollClick(file, event)} // Pass event to handleEnrollClick
                  >
                    Enroll
                  </Button>
                )}

                {(file.isPurchase || file.teacher_id === userId) && (
                  <Button
                    variant="contained"
                    color="primary"
                    onClick={(event) => handleViewClick(file )} // Pass event to handleEnrollClick
                  >
                    View
                  </Button>
                )}
              </Box>
            </Box>
          </Grid>
        ))}
      </Grid>

      {/* Dialog for Enroll Confirmation */}
      <Dialog open={open} onClose={handleClose}>
        <DialogTitle>Enroll Confirmation</DialogTitle>
        <DialogContent>
          <Typography>
            Are you sure you want to enroll in {selectedItem?.title} to Rs.{" "}
            {selectedItem?.price}?
          </Typography>
        </DialogContent>
        <DialogActions>
          <Button onClick={handleClose} color="primary" disabled={loading}>
            Close
          </Button>
          <Button onClick={handleAccept} color="primary" disabled={loading}>
            {loading ? <CircularProgress size={24} /> : "Accept"}
          </Button>
        </DialogActions>
      </Dialog>
    </Box>
  );
};

TileView2.propTypes = {
  papersData: PropTypes.array,
  onTileClick: PropTypes.func.isRequired,
};

export default TileView2;
