// TileView.jsx
import React from 'react';
import { useNavigate } from 'react-router-dom';
import { Box, Button, Typography, Grid } from '@mui/material';
import PropTypes from 'prop-types';

const TileView = ({ papersData = [], onTileClick }) => {
  const navigate = useNavigate();

  const handleTileClick = (item) => {
    console.log("id check",item)
    if (item.id) {
      onTileClick(item); // Notify parent to handle folder click
    }
  };

  return (
    <Box sx={{ padding: 2 }}>
      <Grid container spacing={2}>
        {papersData.map((folder) => (
          <Grid item xs={12} sm={6} md={4} lg={3} key={folder.id}>
            <Button
              onClick={() => handleTileClick(folder)}
              variant="outlined"
              sx={{
                width: '100%',
                height: 0,
                paddingTop: '100%', // 1:1 Aspect Ratio
                position: 'relative',
                display: 'flex',
                flexDirection: 'column',
                justifyContent: 'center',
                alignItems: 'center',
              }}
            >
              <Box
                sx={{
                  position: 'absolute',
                  top: 0,
                  bottom: 0,
                  left: 0,
                  right: 0,
                  display: 'flex',
                  flexDirection: 'column',
                  justifyContent: 'center',
                  alignItems: 'center',
                }}
              >
                <Typography variant="h6">{folder.name}</Typography>
              </Box>
            </Button>
          </Grid>
        ))}
      </Grid>
    </Box>
  );
};

TileView.propTypes = {
  papersData: PropTypes.array.isRequired,
  onTileClick: PropTypes.func.isRequired,
};

export default TileView;
