import React, { useRef, useState, useEffect } from "react";
import { useNavigate, useParams } from "react-router-dom";
import { ref, uploadBytes, getDownloadURL } from "firebase/storage";
import { storage } from "../Components/OtherStuff/firebaseConfig";
import {
  Box,
  Button,
  Container,
  IconButton,
  Typography,
  Paper,
  Grid,
  Card,
  CardContent,
  Dialog,
  DialogActions,
  DialogContent,
  DialogTitle,
  CircularProgress,
  TextField,
  List,
  ListItem,
  ListItemText,
  ListItemAvatar,
  Avatar,
  ListItemSecondaryAction,
} from "@mui/material";
import {
  ArrowBack as ArrowBackIcon,
  Fullscreen as FullscreenIcon,
  Upload as UploadIcon,
  Visibility as VisibilityIcon,
  PlayArrow as PlayArrowIcon,
  Pause as PauseIcon,
  RestartAlt as RestartAltIcon,
  Send as SendIcon,
  Delete as DeleteIcon,
  Edit as EditIcon,
} from "@mui/icons-material";
import axiosClient from "../axiosClient";
import { styled } from "@mui/system";
import { toast, ToastContainer } from "react-toastify";
import "react-toastify/dist/ReactToastify.css";
import profileImage from "../images/avatar.jpg";

const PaperView = () => {
  const navigate = useNavigate();
  const { paperid } = useParams();
  const iframeRef = useRef(null);
  const [time, setTime] = useState(0);
  const [isRunning, setIsRunning] = useState(false);
  const [paperData, setPaperData] = useState({});
  const [paperEnrollData, setPaperEnrollData] = useState({});
  const [overlayVisible, setOverlayVisible] = useState(false);
  const overlayTimeoutRef = useRef(null);
  const [open, setOpen] = useState(false);
  const [studentAnswer, setStudentAnswer] = useState(null);
  const [loading, setLoading] = useState(false);
  const userId = localStorage.getItem("USER_ID");
  const [comments, setComments] = useState([]);
  const [newComment, setNewComment] = useState("");
  const [editingCommentId, setEditingCommentId] = useState(null);
  const [editedCommentText, setEditedCommentText] = useState("");
  const [isPaperLoading, setIsPaperLoading] = useState(true);
  const [isEnrollmentLoading, setIsEnrollmentLoading] = useState(true);
  const [updateAsnwer, setUpdateAnswer] = useState(true);

  const overlayTimeout = 10000;
  const screenshotKeys = ["PrintScreen", "F13", "Snapshot", "SysRq"];

  const UploadButton = styled(Button)({
    marginRight: 16,
  });

  useEffect(() => {
    const fetchPaperData = async () => {
      setIsPaperLoading(true);
      try {
        const response = await axiosClient.get(`/papers/${paperid}/${userId}`);
        if (response.data.status) {
          setPaperData(response.data.data.paper);
          setIsPaperLoading(false);
        } else if (!response.data.status) {
          toast.error(response.data.msg);
          setIsPaperLoading(false);
        }
      } catch (error) {
        console.error("Error fetching paper data:", error);
        setIsPaperLoading(false);
      }
    };

    fetchPaperData();
  }, [paperid]);

  useEffect(() => {
    const fetchPaperEnrollData = async () => {
      setIsEnrollmentLoading(true);
      try {
        const response = await axiosClient.get(
          `/paper-enroll/${paperid}/${userId}`
        );
        if (response.data.status) {
          setPaperEnrollData(response.data.data);
          console.log(response.data.data);
        } else if (!response.data.status) {
          toast.error(response.data.msg);
        }
      } catch (error) {
        console.error("Error fetching paper enrollment data:", error);
      } finally {
        setIsEnrollmentLoading(false);
      }
    };

    fetchPaperEnrollData();
  }, [paperid, userId, updateAsnwer]);

  useEffect(() => {
    const fetchComments = async () => {
      try {
        const response = await axiosClient.get(
          `/comments?limit=100&page=1&paper_id=${paperid}`
        );
        if (response.data.status) {
          setComments(response.data.data.comments);
        } else if (!response.data.status) {
          toast.error(response.data.msg);
        }
      } catch (error) {
        console.error("Error fetching comments:", error);
      }
    };

    fetchComments();
  }, [paperid, newComment]);

  useEffect(() => {
    let timer;
    if (isRunning) {
      timer = setInterval(() => setTime((prevTime) => prevTime + 1), 1000);
    }
    return () => clearInterval(timer);
  }, [isRunning]);

  useEffect(() => {
    const handleKeyDown = (event) => {
      if (screenshotKeys.includes(event.key)) {
        showOverlay();
        console.log("Screenshots are not allowed.");
      }
    };

    const handleBlur = () => {
      showOverlay();
    };

    const handleFocus = () => {
      hideOverlay();
    };

    window.addEventListener("keydown", handleKeyDown);
    window.addEventListener("blur", handleBlur);
    window.addEventListener("focus", handleFocus);

    return () => {
      window.removeEventListener("keydown", handleKeyDown);
      window.removeEventListener("blur", handleBlur);
      window.removeEventListener("focus", handleFocus);
    };
  }, []);

  const uploadFile = async (file, path) => {
    if (file) {
      const fileRef = ref(storage, path);
      try {
        await uploadBytes(fileRef, file);
        const url = await getDownloadURL(fileRef);
        return url;
      } catch (error) {
        console.error("Error uploading file:", error);
        toast.error("Error uploading file. Please try again.");
        throw new Error("Error uploading file. Please try again.");
      }
    } else {
      toast.error("No file selected.");
      throw new Error("No file selected.");
    }
  };

  const handleUploadAnswer = () => {
    setOpen(true);
  };

  const handleClose = () => {
    setOpen(false);
  };

  const handleUpload = (event) => {
    const selectedFile = event.target.files[0];
    if (selectedFile) {
      setStudentAnswer(selectedFile);
    }
  };

  const clickUpload = async () => {
    if (!studentAnswer) {
      toast.error("No file selected.");
      return;
    }

    setLoading(true);
    setUpdateAnswer(false);
    try {
      const student_link = await uploadFile(
        studentAnswer,
        `pdfs/${studentAnswer.name}`
      );
      if (paperEnrollData) {
        const dataToSubmit = {
          _id: paperEnrollData._id,
          student_link,
          status: "to-do-teacher",
          student_answer_time: time,
        };

        await axiosClient
          .put("/paper-enroll/update", dataToSubmit)
          .then((response) => {
            if (response.data.status) {
              toast.success("Answer uploaded successfully");
              setUpdateAnswer(true);
            } else if (!response.data.status) {
              toast.error(response.data.msg);
            }
          })
          .catch((err) => {
            console.error("error in uploading answer" + err);
          });
      }
      setOpen(false);
    } catch (err) {
      toast.error(err.message);
    } finally {
      setLoading(false);
    }
  };

  const showOverlay = () => {
    setOverlayVisible(true);
    clearTimeout(overlayTimeoutRef.current);
    overlayTimeoutRef.current = setTimeout(() => {
      hideOverlay();
    }, overlayTimeout);
  };

  const hideOverlay = () => {
    setOverlayVisible(false);
  };

  const handleFullScreen = () => {
    if (iframeRef.current.requestFullscreen) {
      iframeRef.current.requestFullscreen();
    } else if (iframeRef.current.mozRequestFullScreen) {
      iframeRef.current.mozRequestFullScreen();
    } else if (iframeRef.current.webkitRequestFullscreen) {
      iframeRef.current.webkitRequestFullscreen();
    } else if (iframeRef.current.msRequestFullscreen) {
      iframeRef.current.msRequestFullscreen();
    }
  };

  const handlePlayPause = () => {
    setIsRunning(!isRunning);
  };

  const handleReset = () => {
    setIsRunning(false);
    setTime(0);
  };

  const formatTime = (seconds) => {
    const h = Math.floor(seconds / 3600);
    const m = Math.floor((seconds % 3600) / 60);
    const s = seconds % 60;
    return `${h.toString().padStart(2, "0")}:${m
      .toString()
      .padStart(2, "0")}:${s.toString().padStart(2, "0")}`;
  };

  const handleClickAnswerView = () => {
    navigate(`/viewanswer/${paperid}`);
  };

  const handleCommentSubmit = async () => {
    if (newComment.trim() === "") return;

    try {
      const response = await axiosClient.post("/comments/create", {
        paper_id: paperid,
        user_id: userId,
        comment: newComment,
      });

      if (response.data.status) {
        await setComments((prevComments) => [
          ...prevComments,
          response.data.data,
        ]);
        setNewComment("");
        toast.success("Comment submitted successfully");
      } else if (!response.data.status) {
        toast.error(response.data.msg);
      }
    } catch (error) {
      console.error("Error submitting comment:", error);
    }
  };

  const handleDeleteComment = async (commentId) => {
    try {
      const response = await axiosClient.delete(`/comments/delete/${commentId}`);
      if (response.data.status) {
        await setComments((prevComments) =>
          prevComments.filter((comment) => comment._id !== commentId)
        );
        toast.success("Comment deleted successfully");
      } else if (!response.data.status) {
        toast.error(response.data.msg);
      }

    } catch (error) {
      console.error("Error deleting comment:", error);
    }
  };

  const handleEditComment = (commentId, commentText) => {
    setEditingCommentId(commentId);
    setEditedCommentText(commentText);
  };

  const handleSaveEditedComment = async () => {
    try {
      const response = await axiosClient.put(`/comments/update`, {
        _id: editingCommentId,
        comment: editedCommentText,
        user_id: userId,
      });

      if (response.data.status) {
        setComments((prevComments) =>
          prevComments.map((comment) =>
            comment._id === editingCommentId
              ? { ...comment, comment: editedCommentText }
              : comment
          )
        );
        setEditingCommentId(null);
        setEditedCommentText("");
        toast.success("Comment updated successfully!!!");
      } else if (!response.data.status) {
        toast.error(response.data.msg);
      }

    } catch (error) {
      console.error("Error updating comment:", error);
    }
  };

  return (
    <Container sx={{ mt: 4 }}>
      {overlayVisible && (
        <Box sx={styles.overlay}>
          <Typography sx={styles.overlayText}>Stay focused!</Typography>
        </Box>
      )}
      <Grid container spacing={3}>
        <Grid item xs={12}>
          <Box sx={styles.headerContainer}>
            <IconButton onClick={() => navigate("/")} sx={styles.backButton}>
              <ArrowBackIcon />
            </IconButton>

            <Box sx={styles.headerPaper}>
              <Typography variant="h4" sx={styles.headerTitle}>
                Paper Title: {paperData.title}
              </Typography>
              {paperData.teacher?.full_name && (
                <Typography variant="subtitle1" sx={styles.headerAuthor}>
                  by {paperData.teacher.full_name}
                </Typography>
              )}

              {paperData.subject?.name && (
                <Typography variant="subtitle1" sx={styles.headerAuthor}>
                  Subject: {paperData.subject.name}
                </Typography>
              )}

              {paperData.category?.name && (
                <Typography variant="subtitle1" sx={styles.headerAuthor}>
                  Category: {paperData.category.name}
                </Typography>
              )}

              {paperData.folder?.name && (
                <Typography variant="subtitle1" sx={styles.headerAuthor}>
                  Unit: {paperData.folder.name}
                </Typography>
              )}

              {paperData.duration && (
                <Typography variant="subtitle1" sx={styles.headerAuthor}>
                  Duration: {paperData.duration}
                </Typography>
              )}
            </Box>
          </Box>
        </Grid>

        <Grid item xs={12} md={8}>
          <Card sx={{ height: "100%" }}>
            <CardContent sx={{ position: "relative", height: "60vh", p: 0 }}>
              {isPaperLoading || isEnrollmentLoading ? (
                <CircularProgress />
              ) : (
                <>
                  <iframe
                    ref={iframeRef}
                    src={paperData.paper_link}
                    style={{ width: "100%", height: "100%", border: "none" }}
                    title="PDF Viewer"
                  />

                  <Button
                    onClick={handleFullScreen}
                    startIcon={<FullscreenIcon />}
                    sx={styles.fullScreenButton}
                  >
                    Full Screen
                  </Button>
                  <Typography sx={styles.watermark}>Confidential</Typography>
                </>
              )}
            </CardContent>
          </Card>
        </Grid>

        <Grid item xs={12} md={4}>
          <Paper sx={styles.timerCard}>
            <Typography variant="h6">Timer</Typography>
            <Typography variant="h3" sx={styles.timer}>
              {formatTime(time)}
            </Typography>
            <Box sx={styles.timerActions}>
              <Button
                onClick={handlePlayPause}
                startIcon={isRunning ? <PauseIcon /> : <PlayArrowIcon />}
                variant="contained"
                sx={styles.timerButton}
              >
                {isRunning ? "Pause" : "Start"}
              </Button>
              <Button
                onClick={handleReset}
                startIcon={<RestartAltIcon />}
                variant="outlined"
                sx={styles.timerButton}
              >
                Reset
              </Button>
            </Box>
          </Paper>
          
          <Paper sx={{ ...styles.timerCard, mt: 3 }}>
            <Typography variant="h6">Paper Description</Typography>
            <Typography variant="body1" sx={{ mt: 2 }}>
              {paperData.description}
            </Typography>
          </Paper>
        </Grid>

        <Grid item xs={12}>
          <Card sx={styles.answerCard}>
            <CardContent>
              <Typography variant="h6">Submit Your Answer</Typography>
              <Button
                onClick={handleUploadAnswer}
                startIcon={<UploadIcon />}
                variant="contained"
                color="primary"
                disabled={paperEnrollData && paperEnrollData.student_link ? true : false}
              >
                {paperEnrollData && paperEnrollData.student_link ? 'Answer Uploaded' : 'Upload Answer'}
              </Button>
              <Button
                onClick={handleClickAnswerView}
                startIcon={<VisibilityIcon />}
                variant="contained"
                sx={{ ml: 2 }}
                disabled={paperEnrollData && paperEnrollData.status === "to-do-student" ? true : false}
              >
                View Answer
              </Button>
            </CardContent>
          </Card>
        </Grid>

        <Grid item xs={12}>
          <Card sx={styles.commentCard}>
            <CardContent>
              <Typography variant="h6">Comments</Typography>

              <>
                <List>
                  {comments.map((comment) => (
                    <ListItem key={comment._id}>
                      <ListItemAvatar>
                        <Avatar src={comment.user?.profile_picture || profileImage} />
                      </ListItemAvatar>
                      {editingCommentId === comment._id ? (
                        <TextField
                          fullWidth
                          value={editedCommentText}
                          onChange={(e) => setEditedCommentText(e.target.value)}
                        />
                      ) : (
                        <ListItemText
                          primary={`${comment.user?.first_name} ${comment.user?.last_name}`}
                          secondary={comment.comment}
                        />
                      )}
                      {comment.user_id === userId && (
                        <ListItemSecondaryAction>
                          {editingCommentId === comment._id ? (
                            <IconButton
                              edge="end"
                              aria-label="save"
                              onClick={() =>
                                handleSaveEditedComment()
                              }
                            >
                              <SendIcon />
                            </IconButton>
                          ) : (
                            <>
                              <IconButton
                                edge="end"
                                aria-label="edit"
                                onClick={() =>
                                  handleEditComment(
                                    comment._id,
                                    comment.comment
                                  )
                                }
                              >
                                <EditIcon />
                              </IconButton>
                              <IconButton
                                edge="end"
                                aria-label="delete"
                                onClick={() => handleDeleteComment(comment._id)}
                              >
                                <DeleteIcon />
                              </IconButton>
                            </>
                          )}
                        </ListItemSecondaryAction>
                      )}
                    </ListItem>
                  ))}
                </List>
                <Box sx={styles.commentInputContainer}>
                  <TextField
                    fullWidth
                    variant="outlined"
                    placeholder="Add a comment..."
                    value={newComment}
                    onChange={(e) => setNewComment(e.target.value)}
                  />
                  <IconButton onClick={handleCommentSubmit} color="primary">
                    <SendIcon />
                  </IconButton>
                </Box>
              </>
            </CardContent>
          </Card>
        </Grid>
      </Grid>

      <Dialog open={open} onClose={handleClose}>
        <DialogTitle>Upload Answer</DialogTitle>
        <DialogContent>
          <input
            type="file"
            accept="application/pdf"
            onChange={handleUpload}
          />
          {loading && (
            <Box sx={{ display: "flex", justifyContent: "center", mt: 2 }}>
              <CircularProgress />
            </Box>
          )}
        </DialogContent>
        <DialogActions>
          <Button onClick={handleClose} color="secondary">
            Cancel
          </Button>
          <UploadButton
            onClick={clickUpload}
            startIcon={<UploadIcon />}
            variant="contained"
            color="primary"
            disabled={loading}
          >
            Upload
          </UploadButton>
        </DialogActions>
      </Dialog>
      <ToastContainer />
    </Container>
  );
};

const styles = {
  overlay: {
    position: "fixed",
    top: 0,
    left: 0,
    right: 0,
    bottom: 0,
    backgroundColor: "rgba(0, 0, 0, 0.7)",
    zIndex: 9999,
    display: "flex",
    justifyContent: "center",
    alignItems: "center",
  },
  overlayText: {
    color: "white",
    fontSize: 24,
  },
  backButton: {
    marginRight: 16,
  },
  headerContainer: {
    display: "flex",
    alignItems: "center",
    mb: 3,
  },
  headerPaper: {
    flexGrow: 1,
  },
  headerTitle: {
    fontWeight: "bold",
  },
  headerAuthor: {
    color: "gray",
  },
  fullScreenButton: {
    position: "absolute",
    bottom: 8,
    right: 8,
  },
  watermark: {
    position: "absolute",
    bottom: 0,
    left: "50%",
    transform: "translateX(-50%)",
    opacity: 0.2,
    fontSize: 100,
  },
  timerCard: {
    p: 3,
    display: "flex",
    flexDirection: "column",
    justifyContent: "center",
    alignItems: "center",
    textAlign: "center",
  },
  timer: {
    my: 2,
    fontSize: 64,
  },
  timerActions: {
    display: "flex",
    gap: 2,
  },
  timerButton: {
    minWidth: 120,
  },
  answerCard: {
    mt: 3,
    p: 2,
  },
};

export default PaperView;
